import { ClassUtils } from "./laya/utils/ClassUtils";

export type EnumDescriptor = {
    name: string,
    value: any,
    extend?: EnumDescriptor,
    [index: string]: any,
}[] | any[] | Object | string;

export type PropertType = string | Function | Object | [PropertType] | ["Record", PropertType];

export interface PropertyDescriptor {
    /**  */
    name: string;
    /** 
     * 
     * number,string,boolean,any
     * ["number"]["Record", "number"]Record
     * regClassregClass
     * typeui
     */
    type: PropertType;
    /** name */
    caption: string;
    /**  */
    tips: string;

    /** Inspector */
    catalog: string;
    /*  */
    catalogCaption: string;
    /* */
    catalogOrder: number;

    /**
     * number,string,boolean,color,vec2,vec3,vec4,asset
     * 
     *      number : 
     *      string : multiline
     *      boolean : 
     *      color : ++
     *      vec2 : XY
     *      vec3 : XYZ
     *      vec4 : XYZW
     *      asset : 
     * 
     * 
     * Vector4Vector4color
     * 
     * inspectornullinspectorhiddentruehiddentrue
     * inspectornull
     */
    inspector: string;

    /** :truefalse
     * 1. "!data.a && !data.b"abtrue
     * 2. datadata.adata.babab
     */
    hidden: boolean | string;

    /** 
     * 1. "!data.a && !data.b"abtrue
     * 2. datadata.adata.babab
     */
    readonly: boolean | string;

    /** 
     * 1. 
     * 2. "if(value == data.a) return 'a'"
     * valuedatadata.aa
     */
    validator: string;

    /**  */
    serializable: boolean;
    /** */
    affectBy?: string;

    /**  */
    multiline: boolean;
    /**  */
    password: boolean;
    /** true */
    submitOnTyping: boolean;
    /**  */
    prompt: string;

    /**  */
    enumSource: EnumDescriptor;

    /** true */
    reverseBool: boolean;

    /** nulltrue*/
    nullable: boolean;

    /**  */
    min: number,
    /**  */
    max: number,
    /** minmax */
    range: [number, number];
    /**  */
    step: number;
    /**  */
    fractionDigits: number;
    /**  */
    percentage: boolean;

    /** */
    fixedLength: boolean;
    /** */
    arrayActions: Array<"append" | "insert" | "delete" | "move">;
    /**  */
    elementProps: Partial<PropertyDescriptor>;

    /** a */
    showAlpha: boolean;
    /** defaultdefaultnulldefaultColornull*/
    defaultColor: any;
    /** checkboxnull */
    colorNullable: boolean;

    /** true*/
    hideHeader: boolean;
    /** null*/
    createObjectMenu: Array<string>;

    /**  */
    isAsset?: boolean;
    /** Image,Audio"*/
    assetTypeFilter: string;
    /** stringres://uuidtruefalse*/
    useAssetPath: boolean;
    /**  */
    allowInternalAssets: boolean;

    /** before xxx/after xxx/first/last */
    position: string;

    /** Inspector */
    "private": boolean;

    /**  */
    addIndent: number;

    /** true */
    allowMultipleObjects: boolean;

    /**  */
    hideInDeriveType: boolean;

    /** 
     * func(key?:string)key
     * key 
     */
    onChange: string;

    /**  */
    options: Record<string, any>;
}

export interface TypeDescriptor {
    /** name */
    caption: string;
    /**  */
    menu: string;
    /** */
    icon: string;
    /**  */
    properties: Array<Partial<PropertyDescriptor>>;
    /**  */
    inspector: string;
    /** Componentnull */
    worldType: "2d" | "3d" | null;
    /**  */
    options: Record<string, any>;
}

function dummy() { }

/**
 * 
 */
export function regClass(assetId?: string): any {
    return function (constructor: Function) {
        ClassUtils.regClass(assetId, constructor);
    };
}

/**
 * 
 * @param info 
 */
export function classInfo(info?: Partial<TypeDescriptor>): any { return dummy; }

/**
 * 
 */
export function runInEditor(constructor: Function): void { }

/**
 * 
 * @param info : Number,"number",[Number],["Record", Number]{ type: "string", multiline: true }
 */
export function property(info: PropertType | Partial<PropertyDescriptor>): any { return dummy; }
